const foodWidget = $ => {

	const config = {
		slidesToScroll: 1
	};

	const carouselElement = $('#slider-items');
	const presentations = {
		text: $('#text-items'),
		picture: $('#pictureItems')
	};

	carouselElement.slick({
		infinite: true,
		slidesToShow: 3,
		slidesToScroll: config.slidesToScroll,
		arrows: false,
		asNavFor: '#text-items'
	});

	const textElements = $('#text-items');

	textElements.slick({
		slidesToShow: config.slidesToScroll,
		slidesToScroll: 1,
		arrows: false,
		infinite: true,
		fade: true,
		swipe: false
	});

	const pictureItems = $('#pictureItems');

	pictureItems.slick({
		slidesToShow: config.slidesToScroll,
		slidesToScroll: 1,
		arrows: false,
		infinite: true,
		fade: true,
		swipe: false
	});
};

module.exports = foodWidget;



